import { Link } from 'gatsby'
import React from 'react'

const Footer = props => (
  <footer id="footer">
    <section>
      <h2>Softare Engineering</h2>
      <p>
        Ryan Morris is looking for new exciting projects to apply his technical
        and business prowess. Please reach out via email if there is an opening
        in your software engineering or data ventures.
      </p>
      <ul className="actions">
        <li>
          <Link to="/" className="button">
            Learn More
          </Link>
        </li>
      </ul>
    </section>
    <section>
      <ul className="icons">
        <li>
          <a
            href="https://github.com/RyanJMorris11"
            className="icon fa-github alt"
          >
            <span className="label">GitHub</span>
          </a>
        </li>
        <li>
          <a
            href="https://www.linkedin.com/in/ryanjmorris11/"
            className="icon fa-linkedin alt"
          >
            <span className="label">LinkedIn</span>
          </a>
        </li>
      </ul>

      <dl className="alt">
        <dt>Location</dt>
        <dd>Sacarmento &bull; USA</dd>
        <dt>Email</dt>
        <dd>
          <a href="mailto: ryanjmorris11@gmail.com">RyanJMorris11@gmail.com</a>
        </dd>
      </dl>
    </section>
    <div class="copyright">
      Hand crafted by Ryan Morris <br />
      Made with Gatsby and React/Redux
    </div>
  </footer>
)

export default Footer
